<template>
  <div>
    <Mheader>列表</Mheader>
    <div class="content" ref="scroll" @scroll="scrolload">
      <ul class="bookList">
        <router-link :to="{name:'detail', params :{ bid:book.bookId}}" v-for="(book,index) in books"
         :key="index" tag="li">
          <img v-lazy="book.bookCover" :src="book.bookCover">
          <div class="bookInfo">
            <h3>{{book.bookName}}</h3>
            <p>{{book.bookInfo}}</p>
            <span>{{book.bookPrice}}</span>
            <button @click.stop="remove(book.bookId)" class="colect">删除</button>
            <button class="colect" @click.stop="addCart(book)">添加</button>
          </div>
        </router-link>
        <button @click="getdata" class="more">加载更多</button>
      </ul>

    </div>
  </div>
</template>
<script>
  import Mheader from '../base/Mheader.vue'
  import {pagination,removeBook} from '../api'
  import * as Type from '../store/mutations-type'
  export default {
    data(){
      return {
        books:[],offset:0,hasMore:true,isLoading:false,timer:null
      }
    },
    created(){
      this.getdata()
    },
    methods:{
      addCart(book){
        this.$store.commit(Type.ADD_CART,book);
      },
      scrolload(){
        clearTimeout(this.timer);
        this.timer=setTimeout(()=>{
          let{scrollTop,clientHeight,scrollHeight}=this.$refs.scroll;
          if(scrollTop+clientHeight+20>scrollHeight){
            this.getdata();
          }
        },130);

      },
      async getdata(){
        if(this.hasMore&&!this.isLoading){
          this.isLoading=true;
         let {hasMore,books} = await pagination(this.offset);
         this.books=[...this.books,...books];
         this.hasMore=hasMore;
         this.offset=this.books.length;
         this.isLoading=false;
        }
      },
      async remove(id){
        await removeBook(id);
        this.books=this.books.filter(item=>item.bookId!=id);
      }
    },
    computed:{},
    components:{
      Mheader
    }
  }
</script>
<style scoped  lang="less">

  .bookList{
    position: relative;
    padding:10px;
    padding-top: 20px;
    padding-bottom: 150px;
    li{
      overflow: hidden;
      padding-bottom: 20px;
      padding-top: 20px;
      border-bottom: 1px dashed #cccccc;
    }
    img{
      width: 38%;
      float: left;
    }
    .bookInfo {
      width: 60%;
      float: left;
      h3 {
        font-size: 16px;
        line-height: 20px;
        height: 40px;
        overflow: hidden;
        margin-top: 5px;
        margin-bottom: 5px;
      }
      p {
        font-size: 14px;
        line-height: 18px;
        height: 18px;
        overflow: hidden;
        margin-bottom: 5px;
      }
      span {
        color: red;
        display: block;
      }
      .colect {
        display: inline-block;
        padding:2px 10px;
        appearance: none;
        border:none;
        outline: none;
        background-color: orangered;
        color: #ffffff;
        border-radius: 5px;
        margin-top: 5px;
      }
    }
    .more{
      position: absolute;
      bottom:100px;
      left:50% ;
      width: 100px;
      height: 30px;
      margin-left: -50px;
      line-height: 30px;
      text-align: center;
      background-color: orangered;
      border: none;
      border-radius: 10px;
      outline: none;
    }
  }
</style>



